import React, { Component } from "react";
import Card from "react-bootstrap/Card";
import CustomButton from "./CustomButton";
const styles = {
  center: { display: "flex", justifyContent: "center", alignItems: "center" },
};

class PricingCard extends Component {
  render() {
    return (
      <Card
        style={{ width: "22rem", margin: 30, marginTop: 10, marginBottom: 10 }}
      >
        <div style={{ ...styles.center, backgroundColor: "lightgrey" }}>
          <h2 style={{ margin: 0, fontWeight: 300 }}>{this.props.title}</h2>
        </div>
        <Card.Body>
          <Card.Title style={styles.center}>{this.props.price}</Card.Title>
          {this.props.desc}
        </Card.Body>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginBottom: 10,
          }}
        >
          <CustomButton
            variant="primary"
            activeClass="active"
            className="btn"
            type="submit"
            value="Test 2"
            to="contact"
            spy={true}
            smooth={true}
            offset={-this.props.offset}
            duration={500}
          >
            Contact
          </CustomButton>
        </div>
      </Card>
    );
  }
}

export default PricingCard;
